import React from 'react';
import Layout from '../../components/Layout';
import Shipping from './Shipping';


function action() {
  return {
    chunks: ['Shipping'],
   
    component: (
      <Layout>
        <Shipping/>
      </Layout>
    ),
  };
}

export default action;



